.gs-symbolizers {

  .gs-symbolizers-content {
    display: flex;

    >*{
      margin: 5px;
    }
  }

  .gs-add-button {
    font-size: xxx-large;
    display: flex;
    align-items: center;
    justify-content: center;

    color: lightgrey;
    border-radius: 4px;
    border: 1px solid lightgrey
  }

  .gs-symbolizer-card {
    margin: 5px;
  }

  .gs-symbolizers-grid {

    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    flex-wrap: wrap;
  }

  .gs-symbolizers-list {
    display: flex;
    overflow: hidden;
  }

  .gs-symbolizers-footer {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
  }

}
