.gs-filter-tree {
  text-align: left;

  &.ant-tree {
    .style-filter-node.and-filter,
    .style-filter-node.or-filter,
    .style-filter-node.not-filter {
      border-radius: 4px;
      margin-bottom: 5px;
    }

    .style-filter-node {
      width: 100%;

      &.comparison-filter {

        .gs-comparison-filter-ui {
          padding-left: 0;
        }

        .ant-form-item {
          .ant-form-item-control-input {
            min-height: unset;
          }
        }

        .ant-tree-switcher {
          display: none;
        }
      }

      .ant-tree-node-content-wrapper {
        flex: 1;
        height: inherit;
        padding: 0;
      }

      .node-title {
        display: flex;
        align-items: baseline;
        justify-content: space-between;

        .filter-menu-button {
          margin-left: 0.5em;
          visibility: hidden;
        }
      }

      &:hover {
        .node-title {
          .filter-menu-button {
            visibility: visible;
          }
        }
      }
    }

  }
}
